<template>
  <label class="checkbox">
    <input
      v-model="value"
      class="checkbox__field"
      type="checkbox"
    />
    <Icon class="checkbox__icon" :name="value ? 'checked' : 'unchecked'" />
    <span class="checkbox__label" v-html="label" />
  </label>
</template>

<script>
import Icon from '@/components/Icon/Icon'
import { useModel } from '@/utils/useModel'

export default {
  name: 'Checkbox',
  components: { Icon },
  emits: ['update:modelValue'],
  props: {
    label: {
      type: String,
      require: true
    },
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    return {
      value
    }
  }
}
</script>

<style lang="scss">
.checkbox {
  display: flex;
  align-items: center;
  width: fit-content;
  cursor: pointer;

  &__icon {
    margin-right: 14px;
  }
  &__field {
    display: none;
  }
  &__label {
    font-size: 12px;
    line-height: 14px;
  }
}
</style>
