<template>
  <section class="deposits-history">
    <div class="deposits-history__filters">
      <div class="deposits-history__filters-main">
        <Input
          class="deposits-history__filters-main__search"
          hide-shadow
          :placeholder="t('common.search')"
        >
          <template #prepend>
            <Icon name="search" />
          </template>
        </Input>
        <SelectInput
          muted
          :placeholder="t('common.crypto')"
          icon="chevron-down"
          class="deposits-history__filters-main__crypto"
        />
        <SelectInput
          muted
          :placeholder="t('common.to')"
          icon="chevron-down"
          class="deposits-history__filters-main__to"
        />
        <SelectInput
          muted
          :placeholder="t('common.from')"
          icon="chevron-down"
          class="deposits-history__filters-main__from"
        />
      </div>
      <div class="deposits-history__filters-secondary">
        <RadioButtons v-model="depositsStatus" :data="radioFilter">
          <template #default="{ item }">
            {{ t(item.title) }}
          </template>
        </RadioButtons>
        <SelectColumns :columns="columns" />
      </div>
    </div>
    <Table :columns="columns" :data="data">
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ item, column }">
        <Badge
          v-if="column.name === 'status'"
          :type="COLORS[item]"
        >
          {{ t(STATUS[item]) }}
        </Badge>
        <Date
          v-else-if="column.type === 'date'"
          :date="item"
          format="DD MMM YYYY HH:mm"
        />
        <template v-else>{{ item }}</template>
      </template>
    </Table>
    <Pagination
      class="deposits-history__pagination"
      v-model="page"
      :per-page="perPage"
      :total-count="totalCount"
    />
  </section>
</template>

<script>
import { computed, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import { useActions, useGetters, useMutations } from '@/plugins/vuex'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import SelectInput from '@/components/UIKit/Select/SelectInput'
import RadioButtons from '@/components/UIKit/RadioButtons'
import SelectColumns from '@/components/Table/SelectColumns'
import Table from '@/components/Table/Table'
import Badge from '@/components/UIKit/Badge'
import { COLORS, STATUS } from '@/common/status'
import { radioFilter } from '@/store/mock/depositsMock'
import { MODULES_NAMES } from '@/store/modules'
import Pagination from '@/components/UIKit/Pagination'
import Date from '@/components/Text/Date'

export default {
  name: 'DepositsHistoryTab',
  components: {
    Date,
    Pagination,
    Table,
    SelectColumns,
    SelectInput,
    Input,
    Icon,
    RadioButtons,
    Badge
  },
  setup () {
    const { t } = useI18n()
    const depositsStatus = ref({})
    const {
      getHistoryColumns: columns,
      getHistoryData: data,
      getHistoryPage,
      getHistoryPerPage: perPage,
      getHistoryTotalCount: totalCount
    } = useGetters(MODULES_NAMES.DEPOSITS)

    const {
      SET_HISTORY_PAGE: setHistoryPage
    } = useMutations(MODULES_NAMES.DEPOSITS)
    const {
      setMockHistoryData
    } = useActions(MODULES_NAMES.DEPOSITS)

    const page = computed({
      get: () => getHistoryPage.value,
      set: setHistoryPage
    })

    setMockHistoryData()

    return {
      t,
      depositsStatus,
      page,
      perPage,
      totalCount,
      radioFilter,
      columns,
      data,
      COLORS,
      STATUS
    }
  }
}
</script>

