  position: relative;
  flex-grow: 1;
  padding-top: 16px;
  padding-bottom: 60px;

  &__filters {
    margin-bottom: 20px;

    &-main {
      display: flex;

      &__search {
        flex-grow: 1;
        height: 44px;
      }
      &__crypto {
        margin: 0 20px;

        .select-input__placeholder {
          opacity: 0.6;
        }
      }
      &__to {
        margin-right: 9px;
        .select-input__placeholder {
          opacity: 1;
        }
      }
      &__from {
        .select-input__placeholder {
          opacity: 1;
        }
      }
    }
    &-secondary {
      display: flex;
      justify-content: space-between;
      align-items: center;
