<template>
  <div class="radio-buttons">
    <div
      v-for="item in data"
      :key="item.key"
      class="radio-buttons__item"
      :class="{ 'radio-buttons__item_selected': value?.key === item.key }"
      @click="selectItem(item)"
    >
      <slot :item="item" />
    </div>
  </div>
</template>

<script>
import { useModel } from '@/utils/useModel'

export default {
  name: 'RadioButtons',
  emits: ['update:modelValue'],
  props: {
    data: {
      type: Array,
      require: true
    },
    modelValue: {
      type: [Object, null],
      default: null,
      validate (val) {
        if (!val) return true
        return !!val.key
      }
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    const selectItem = (val) => {
      value.value = val
    }

    return {
      value,
      selectItem
    }
  }
}
</script>

<style lang="scss">
.radio-buttons {
  display: flex;
  overflow: hidden;

  &__item {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-color: #E6E8ED;
    border-width: 1px;
    border-style: solid;
    border-right-color: transparent !important;
    background: #FFFFFF;
    cursor: pointer;

    &:first-child {
      border-radius: 4px 0 0 4px;
    }
    &:last-child {
      border-right-color: #E6E8ED !important;
      border-radius: 0 4px 4px 0;
    }
    &_selected, &_selected:last-child {
      background: $selectedBackground;
      border-color: $selectedBorder !important;
    }
  }
}
</style>
